import "bootstrap/dist/css/bootstrap.min.css";

import { useState } from "react";
import data from "./data";
import People from "./People";

function App() {
  const [people, setPeople] = useState(data);
  return (
    <main className="mt-5">
      <section className="container-pink d-flex  flex-column">
        <h3 className="text-center mt-5">{people.length} birthdays today</h3>
        <People people={people} />
        <button className="btn btn-gray" onClick={() => setPeople([])}>
          clear all
        </button>
      </section>
    </main>
  );
}

export default App;
