import React from "react";

const People = ({ people }) => {
  const renderdPeople = people.map((person) => {
    return (
      <div key={person.id} className="d-flex mx-4  align-items-center">
        <img src={person.image} alt={person.name} />
        <div>
          <h4>{person.name}</h4>
          <p>{person.age} years</p>
        </div>
      </div>
    );
  });
  return <>{renderdPeople}</>;
};

export default People;
